<template>
    <div id="addJurisdiction">
        <p class="title_main">新增授权</p>
  <!--      <div class="view_main">
            <span>公司邮箱</span>
            <el-input class="input_type1"  placeholder="公司邮箱"></el-input>
            <span>姓名</span>
            <el-input class="input_type1"  placeholder="姓名"></el-input>
        </div>-->
        <div  class="view_main">
            <span>职能</span>
            <template v-if="rolesList.length>0">
                <el-select v-model="rolesValue" placeholder="请选择">
                    <el-option
                    v-for="item in rolesList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                    </el-option>
                </el-select>
            </template>
        </div>
        <div class="view_main checkbox_main">
            <p>权限勾选</p>
            <div style="overflow:hidden">
                <div class="checkbox_menu"  v-for="(data,index) in dataMenu">
                    <el-checkbox :indeterminate="data.isIndeterminate" v-model="data.checkAll" @change="handleCheckAllChange(data.isIndeterminate,index)">{{data.typeName}}</el-checkbox>
                    <div style="margin: 15px 0;">
                        <el-checkbox-group  v-model="data.checkedCities" @change="handleCheckedCitiesChange(data.checkedCities,index)">
                            <el-checkbox class="sub_checkbox" v-for="city in data.cities" :label="city" :key="city">{{city}}</el-checkbox>
                        </el-checkbox-group>
                    </div>
                </div>
            </div>
        </div>
        <div class="view_main">
            <el-button v-if="dealType == 0" @click="addJurisdictions()">新增</el-button>
            <el-button v-if="dealType == 1" @click="editJurisdictions()">编辑</el-button>
            <el-button v-if="dealType == 2" @click="saveJurisdictions()">保存</el-button>
            <el-button v-if="dealType == 2" @click="cancelEvnet()">取消</el-button>
        </div>

    </div>
</template>


<script>
export default {
    data(){
        return{
            dealType:"",
            dataMenu:[
                {checkAll: false,
                    typeName:"全局",
                    checkedCities: [],
                    cities: ['管理权限', '标签管理'],
                    cityOptions:['管理权限', '标签管理'],
                    isIndeterminate: true
                },
                {checkAll: false,
                    typeName:"题库",
                    checkedCities: [],
                    cities: ['专业列表', '课题列表',"测试列表"],
                    cityOptions:['专业列表', '课题列表',"测试列表"],
                    isIndeterminate: true
                },
                {checkAll: false,
                    typeName:"实战队伍",
                    checkedCities: [],
                    cities: ['队伍列表', '队伍属性'],
                    cityOptions:['队伍列表', '队伍属性'],
                    isIndeterminate: true
                },
                {checkAll: false,
                    typeName:"圈子",
                    checkedCities: [],
                    cities: ['圈子列表', '圈子列表管理'],
                    cityOptions:['圈子列表', '圈子列表管理'],
                    isIndeterminate: true
                },
                {checkAll: false,
                    typeName:"帖子",
                    checkedCities: [],
                    cities: ['帖子列表'],
                    cityOptions:['帖子列表'],
                    isIndeterminate: true
                },
                {checkAll: false,
                    typeName:"用户管理",
                    checkedCities: [],
                    cities: ['用户管理'],
                    cityOptions:['用户管理'],
                    isIndeterminate: true
                }
            ],
            rolesList: [],
            rolesValue: ''
        }
    },
    methods: {
        dealEvent(){
            var path = this.$route.name;
            console.log(path);
            if(path == "seeJurisdiction"){
                this.dealType = 1;
            }else if(path == "editJurisdiction"){
                this.dealType = 2;
            }else{
                this.dealType = 0;
            }
        },
        handleCheckAllChange(val,index) {
            this.dataMenu[index].checkedCities = val ? this.dataMenu[index].cityOptions : [];
            this.dataMenu[index].isIndeterminate = !this.dataMenu[index].isIndeterminate;
        },
        handleCheckedCitiesChange(value,index) {
            let checkedCount = value.length;
            this.dataMenu[index].checkAll = checkedCount === this.dataMenu[index].cities.length;
            this.dataMenu[index].isIndeterminate = checkedCount > 0 && checkedCount < this.dataMenu[index].cities.length;
        },
        //http://admin.suishi.com/admin/admin_user/roles
        rolesListEvent(){
            this.rolesList =  [
                {
                    "id": 2,
                    "name": "管理员",
                    "slug": " ",
                    "created_at": null,
                    "updated_at": null
                },
                {
                    "id": 3,
                    "name": "运营",
                    "slug": " ",
                    "created_at": null,
                    "updated_at": null
                },
                {
                    "id": 4,
                    "name": "开发",
                    "slug": " ",
                    "created_at": null,
                    "updated_at": null
                },
                {
                    "id": 5,
                    "name": "产品",
                    "slug": " ",
                    "created_at": null,
                    "updated_at": null
                },
                {
                    "id": 6,
                    "name": "市场",
                    "slug": " ",
                    "created_at": null,
                    "updated_at": null
                },
                {
                    "id": 7,
                    "name": "测试",
                    "slug": " ",
                    "created_at": null,
                    "updated_at": null
                },
                {
                    "id": 8,
                    "name": "实习生",
                    "slug": " ",
                    "created_at": null,
                    "updated_at": null
                }
            ]
        },
        addJurisdictions(){

        },
        saveJurisdictions(){

        },
        editJurisdictions(){
            this.$router.push("/editJurisdiction");
            this.dealType = 2;
        },
        cancelEvnet(){
            this.$router.push("/jurisdiction");
        }
    },
    mounted:function(){
        this.rolesListEvent();
        this.dealEvent();
    }
}
</script>
    <style>
        #addJurisdiction{
            text-align:left;
        }
        .title_main{
            padding:20px 0;
        border-bottom: 1px solid #999;
        }
        .input_type1{
            width:200px;
        }
        .view_main{
            margin-top:10px
        }
        .checkbox_main{
            padding:10px;
            background:#efefef;
        }
        .checkbox_menu{
            width:16%;
        float:left;
        }
        .sub_checkbox{
        display: block;
        }
    </style>


